.presurvey-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #FFFFFF;
  padding: 20px;
  max-width: 800px; 
  margin: 20px auto;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  opacity: 0; 
}

.presurvey-container h2 {
  margin-bottom: 20px;
  font-size: 24px;
  color: #A77BCA;
  text-align: center;
}

.presurvey-field {
  margin-bottom: 15px;
  width: 100%;
  max-width: 700px; 
}

.presurvey-field label {
  display: block;
  margin-bottom: 8px;
  font-size: 16px;
  color: #333;
}

.presurvey-field select {
  width: 100%;
  padding: 10px;
  font-size: 14px;
  border: 1px solid #D6A6E0;
  border-radius: 4px;
  box-sizing: border-box;
  background-color: #FFFFFF;
  color: #333;
}

.presurvey-field select:focus {
  border-color: #A77BCA;
  outline: none;
}

.options {
  display: flex;
  justify-content: space-between;
}

.options label {
  display: flex;
  align-items: center;
  margin-right: 10px;
}

.options input[type="radio"] {
  margin-right: 5px;
}

button[type="submit"] {
  padding: 10px 20px;
  font-size: 16px;
  color: #FFFFFF;
  background-color: #BFA2D8;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button[type="submit"]:hover {
  background-color: #A77BCA;
}